@charset "UTF-8";
/**
 * @site         Kosméline
 * @author       Luc Poupard
 * @lastmodified 2020-12-21
 * @note         Woocommerce
 */

/* Basket in menu */
.cart-contents-count {
	position: absolute;
	top: 50%;
	right: 4rem;
	display: inline-block;
	margin-top: -1.5rem;
	width: 3rem;
	height: 3rem;
	border-radius: 50%;
	background-color: $base-text-color;
	color: $base-background-color;
	text-align: center;
	text-indent: -0.2rem;
	line-height: 1.4;

	&:empty {
		display: none;
	}

	> span {
		display: none;
	}
}

.menu-basket {
	a {
		padding-right: 8rem;

		&:hover,
		&:focus {
			.cart-contents-count {
				background-color: $base-background-color;
				color: $base-text-color;
			}
		}
	}
}

/* Breadcrumb */
.woocommerce-breadcrumb {
	padding: calc(#{$content-padding} / 2) $content-padding 1.25rem;
	background-color: $color-2;
	color: $color-4;

	a {
		color: currentColor;
	}
}

/* Products list */
.woocommerce-products-header {
	margin: calc(#{$content-padding} / 2) $content-padding;
}

.woocommerce-result-count,
.woocommerce-ordering {
	margin: 0;
	padding: 1rem $content-padding;
	width: 49%;
}

.woocommerce-result-count {
	float: left;
}

.woocommerce-ordering {
	float: right;
	text-align: right;
}

.products {
	clear: both;
	margin: 0;
	padding: 0;
	list-style: none;
	display: flex;
	flex-wrap: wrap;

	.archive & {
		padding: $content-padding;
	}

	.product {
		width: calc((100% - 9rem) / 4);
		border: 2px solid $color-3;
		background-color: $color-3;
		text-align: center;

		&:not(:nth-child(4n)) {
			margin-right: 3rem;
		}

		&:nth-child(4n) ~ .product {
			margin-top: 3rem;
		}

		&:hover {
			border-color: $color-4;
		}

		&:focus-within {
			border-color: $color-4;
		}
	}

	a {
		text-decoration: none;
	}

	.woocommerce-loop-product__link {
		display: block;

		.price {
			margin: 1rem;
			text-align: right;
		}
	}

	.woocommerce-loop-category__title,
	.woocommerce-loop-product__title {
		margin: 1rem;
		font-size: 2rem;
		text-align: left;
		line-height: 1.25;
	}

	.woocommerce-loop-category__title {
		mark {
			background-color: transparent;
			color: currentColor;
			font-weight: normal;
		}
	}

	.add_to_cart_button {
		display: inline-block;
		margin: 1.5rem auto 2rem;
		padding: 0.25em 0.5em 0.35em;
		// background-color: $color-4;
		// color: $base-background-color;
		border: 2px solid $base-text-color;
		background-color: $color-3;
		color: $base-text-color;

		// &:hover,
		// &:focus {
		// 	background-color: $base-text-color;
		// 	color: $color-1;
		// }

		// &:active {
		// 	background-color: $base-text-color;
		// 	color: $color-3;
		// }

		&:hover,
		&:focus {
			border-color: $color-4;
			color: $color-4;
		}

		&:active {
			background-color: $base-text-color;
			color: $color-3;
		}
	}
}

/* Product detail */
.product {
	background-color: $color-1;
}

.woocommerce-product-gallery__wrapper {
	margin: 0;
}

.product-kosmeline {
	display: flex;
	padding: $content-padding;

	.product_title {
		line-height: 1.25;
	}

	.woocommerce-product-gallery {
		width: 35rem;
		max-width: 35%;

		img {
			border: 2px solid $color-4;
		}
	}

	.entry-summary {
		flex: 1;
		padding-left: $content-padding;
	}
}

.woocommerce-tab {
	margin-top: 2rem;
}

.woocommerce-Tabs-panel {
	padding: calc(#{$content-padding} / 2) $content-padding;
	background-color: $color-3;
}

.wc-tabs {
	margin: 0;
	padding: 0;
	list-style: none;

	li {
		display: inline-block;

		&.active {
			background-color: $color-3;

			a {
				text-decoration: none;
			}
		}
	}

	a {
		display: inline-block;
		padding: 1.75rem $content-padding;
	}
}

.entry-summary {
	.price,
	.cart {
		text-align: right;
	}

	.price {
		font-family: $font-stack-headings;
		font-size: 2.4rem;
		font-weight: bold;
	}

	.cart {
		.quantity {
			display: inline-block;
		}

		input,
		button {
			display: inline-block;
			border: 2px solid $base-text-color;
			background-color: $base-text-color;
			color: $base-background-color;
			line-height: 1.5;
		}

		input {
			padding: 0.75em 0.5em;

			&:focus {
				border-color: $color-4;
			}
		}

		button {
			padding: 0.75em 1em;
			background-color: $color-3;
			color: $base-text-color;
			cursor: pointer;

			&:hover,
			&:focus {
				border-color: $color-4;
				color: $color-4;
			}

			&:active {
				background-color: $base-text-color;
				color: $color-3;
			}
		}
	}
}

.woocommerce-product-details__short-description {
	margin: 2rem 0;
	font-size: 2rem;
}

.product_meta {
	margin-top: 2rem;
	color: $color-4;

	a {
		color: currentColor;
	}

	> span {
		display: block;
	}
}

/* Basket */
.shop_table {
	width: 100%;

	th,
	td {
		padding: 1rem;
	}

	.product-name {
		text-align: left;
	}

	.product-remove,
	.product-thumbnail,
	.product-quantity {
		text-align: center;
	}

	.product-price,
	.product-subtotal,
	.actions {
		text-align: right;
	}

	.product-thumbnail {
		img {
			width: 10rem;
			vertical-align: middle;
		}
	}

	.actions {
		button {
			display: inline-block;
			border: none;
			background-color: transparent;

			&:not([disabled]) {
				text-decoration: underline;
				color: currentColor;
				cursor: pointer;
			}

			&:hover,
			&:focus {
				text-decoration: none;
			}
		}
	}
}

.woocommerce-cart-form__contents {
	tbody {
		tr {
			td {
				border-top: 2px solid $color-4;
			}
		}
	}

	.cart_item:not(:first-child) {
		td {
			border-top: 1px dotted;
		}
	}
}

.cart-collaterals {
	margin: 3rem 0 0 auto;
	width: calc(100% - 60rem);

	th {
		text-align: left;
		vertical-align: top;
	}

	.shop_table {
		tr:not(:first-child) {
			th,
			td {
				border-top: 2px solid $color-4;
			}
		}
	}
}

.woocommerce-shipping-methods {
	padding: 0;
	list-style: none;

	input[type="radio"] {
		margin-right: 0.5rem;
	}
}

.wc-proceed-to-checkout {
	padding-top: 2rem;
	text-align: right;

	.checkout-button {
		display: inline-block;
		padding: 0.75em 1em;
		border: 2px solid $base-text-color;
		background-color: $color-3;
		color: $base-text-color;
		line-height: 1.5;
		text-decoration: none;

		&:hover,
		&:focus {
			border-color: $color-4;
			color: $color-4;
		}

		&:active {
			background-color: $base-text-color;
			color: $color-3;
		}
	}
}

/* Validation commande */
#customer_details {
	display: flex;
	justify-content: space-between;

	> div {
		padding: 3rem 0;
		width: calc(50% - 3rem);
	}

	label {
		display: block;
		margin: 1rem 0 0.5rem;
	}
}

// Review
.woocommerce-checkout-review-order-table {
	border-collapse: collapse;

	tbody tr,
	tfoot tr {
		th,
		td {
			border-top: 2px solid $color-4;
		}
	}

	.cart_item:not(:first-child),
	.shipping {
		th,
		td {
			border-top: 1px dotted;
		}
	}

	.product-subtotal {
		text-align: left;
	}

	.cart-subtotal,
	.shipping,
	.order-total {
		th {
			text-align: right;
			vertical-align: top;
		}
	}
}

.place-order {
	margin-top: 3rem;
	text-align: center;

	button {
		display: inline-block;
		padding: 0.75em 1em;
		border: 2px solid $base-text-color;
		background-color: $color-3;
		color: $base-text-color;
		line-height: 1.5;
		text-decoration: none;
		cursor: pointer;

		&:hover,
		&:focus {
			border-color: $color-4;
			color: $color-4;
		}

		&:active {
			background-color: $base-text-color;
			color: $color-3;
		}
	}
}

.woocommerce-checkout-payment {
	margin-top: 3rem;

	.methods {
		padding: 0;
	}

	.payment_box {
		margin: 1rem 2rem 2rem;
		padding: 1rem 2rem;
		border: 1px dashed $color-2;
		background-color: $base-text-color;
		color: $color-3;
	}

	.woocommerce-privacy-policy-text {
		text-align: left;
	}
}

// Repris de la quantité sur le détail
input[type="text"],
input[type="number"],
input[type="tel"],
input[type="email"],
textarea,
select {
	display: block;
	padding: 0.25em 0.5em;
	width: 100%;
	border: 2px solid $base-text-color;
	background-color: $base-text-color;
	color: $base-background-color;
	line-height: 1.5;

	&:focus {
		border-color: $color-4;
	}
}

textarea {
	width: 100%;
}

.select2-container--default .select2-selection--single {
	padding: 0.25em 0.5em;
	border: 2px solid $base-text-color;
	border-radius: 0;
	background-color: $base-text-color;
	height: auto;

	&:focus {
		border-color: $color-4;
	}
}

.select2-container--default
	.select2-selection--single
	.select2-selection__rendered {
	line-height: inherit;
}

.select2-container .select2-selection--single .select2-selection__rendered {
	padding-left: 0;
}

.select2-container--default
	.select2-selection--single
	.select2-selection__arrow {
	height: 100%;
}

.select2-dropdown,
.select2-container--default
	.select2-selection--single
	.select2-selection__rendered {
	color: $base-background-color;
}

.select2-container--default
	.select2-results__option--highlighted[aria-selected],
.select2-container--default
	.select2-results__option--highlighted[data-selected] {
	background-color: $color-3;
	color: $base-text-color;
}

.select2-dropdown {
	border: 2px solid $color-4;
	border-radius: 0;
}

.select2-container--open .select2-dropdown--below {
	border-top: 2px solid $color-4;
}

/**/
.blockUI.blockOverlay {
	background-color: rgba($color-1, 0.85) !important;
}

.woocommerce-message {
	margin: 2rem;
	padding: 1rem 2rem;
	border: 1px dashed $color-2;
	background-color: $base-text-color;
	color: $color-3;

	a {
		color: currentColor;
	}
}

.added_to_cart.wc-forward {
	display: block;
	margin: 0 auto 2rem;
	text-decoration: underline;
}

/* Confirmation */
.woocommerce-table--order-details {
	border-collapse: collapse;

	tbody tr,
	tfoot tr {
		th,
		td {
			border-top: 2px solid $color-4;
		}
	}

	.order_item:not(:first-child),
	tfoot tr:not(:first-child):not(:last-child) {
		th,
		td {
			border-top: 1px dotted;
		}
	}

	.product-total {
		text-align: left;
	}

	tfoot {
		th {
			text-align: right;
			vertical-align: top;
		}
	}
}

.woocommerce-order-details,
.woocommerce-customer-details {
	margin-top: 4rem;
}

.woocommerce-columns--addresses {
	display: flex;
	justify-content: space-between;

	> div {
		padding: 3rem 0;
		width: calc(50% - 3rem);
	}
}
