/**
 * @site         Kosméline
 * @author       Luc Poupard
 * @lastmodified 2020-12-21
 * @note         Styles de base
 */
/* ----------------------------- */
/* Sommaire */
/* ----------------------------- */
/*
	== Variables
	== Styles de base
	== Typographies
		-- Titres
		-- Marges
	== Images
*/
/* == @section Variables ==================== */
/**
 * On définit une valeur par défaut des variables présentes dans ce fichier.
 * Pour définir une autre valeur, il suffit de lui définir une valeur
 * dans le fichier _config.scss du projet.
 */
/* Titres : valeurs en pixels */
/**
 * Par défaut, on utilise les polices système
 * @see https://bitsofco.de/the-new-system-font-stack/
 */
/**
 * Pour éviter la fusion des marges
 * False = pas de margin-top si l'élément est le premier enfant de son parent
 */
/* == @section Styles de base ==================== */
* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block; }

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline; }

audio:not([controls]) {
  display: none;
  height: 0; }

/* == @section Typographies ==================== */
/* @note La base correspond à 10px */
html {
  font-size: 62.5%;
  /* IE9-IE11 math fixing. Voir http://bit.ly/1g4X0bX */
  /* Merci à @guardian, @victorbritopro, @eQRoeil & Knacss */
  font-size: calc(1em * 0.625); }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  font-size: 1.6em;
  line-height: 1.5; }

/* -- @subsection Titres ----------------------------- */
h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  display: block;
  font-family: "inglobal";
  font-weight: bold; }

h1,
.h1 {
  font-size: 2em;
  line-height: 1.5;
  margin: 0.75em 0 0 0; }

h2,
.h2 {
  font-size: 1.75em;
  line-height: 1.71429;
  margin: 0.85714em 0 0 0; }

h3,
.h3 {
  font-size: 1.5em;
  line-height: 1;
  margin: 0.5em 0 0 0; }

h4,
.h4 {
  font-size: 1.25em;
  line-height: 1.2;
  margin: 1.2em 0 0 0; }

h5,
.h5 {
  font-size: 1.125em;
  line-height: 1.33333;
  margin: 1.33333em 0 0 0; }

h6,
.h6 {
  font-size: 1em;
  line-height: 1.5;
  margin: 1.5em 0 0 0; }

/* -- @subsection Marges ----------------------------- */
/* Éviter la fusion des marges sur les premiers éléments d'un bloc */
p:first-child,
ul:first-child,
ol:first-child,
dl:first-child,
blockquote:first-child,
h1:first-child,
.h1:first-child,
h2:first-child,
.h2:first-child,
h3:first-child,
.h3:first-child,
h4:first-child,
.h4:first-child,
h5:first-child,
.h5:first-child,
h6:first-child,
.h6:first-child {
  margin-top: 0; }

/* Éviter marges supplémentaires sur éléments imbriqués */
li p,
li ul,
li ol {
  margin-bottom: 0;
  margin-top: 0; }

/* == @section Images ==================== */
img {
  max-width: 100%;
  height: auto; }

