/**
 * @site         Kosméline
 * @author       Luc Poupard
 * @lastmodified 2020-12-21
 * @note         Styles de base pour les formulaires
 */
/* ----------------------------- */
/* Sommaire */
/* ----------------------------- */
/*
	== Styles de base
*/
/* == @section Styles de base ==================== */
/* Supprimer la croix pour effacer la valeur d'un champ (IE) */
::-ms-clear {
  display: none; }

/**
 * Éviter le zoom lorsqu'on prend le focus sur un champ sur iOS
 * @see http://stackoverflow.com/questions/2989263/disable-auto-zoom-in-input-text-tag-safari-on-iphone
 */
@media screen and (-webkit-min-device-pixel-ratio: 1.5) {
  select,
  textarea,
  input[type="text"],
  input[type="password"],
  input[type="datetime"],
  input[type="datetime-local"],
  input[type="date"],
  input[type="month"],
  input[type="time"],
  input[type="week"],
  input[type="number"],
  input[type="email"],
  input[type="url"],
  input[type="search"],
  input[type="tel"],
  input[type="color"] {
    font-size: 1em; } }

/**
 * Désactiver les arrondis sur iOS
 */
input[type="submit"],
input[type="reset"],
input[type="button"],
button {
  -webkit-appearance: none; }

/**
 * Styler les placeholder
 * #see http://stackoverflow.com/questions/2610497/change-an-inputs-html5-placeholder-color-with-css
 */
/* WebKit, Blink, Edge */
/* Mozilla Firefox 19+ */
/* Internet Explorer 10-11 */
/* On limite le resize des textarea dans le sens veritcal pour éviter de tout péter */
textarea {
  resize: vertical; }

/**
 * @site         Kosméline
 * @author       Luc Poupard
 * @lastmodified 2020-12-21
 * @note         Styles de base pour WordPress
 */
/* ----------------------------- */
/* Sommaire */
/* ----------------------------- */
/*
	== Accessibilité
	== Alignements
	== Réinitialisation flottants
	== Widgets
	== Contenus
		-- Articles et pages
		-- Commentaires
	== Médias
		-- Légendes
		-- Galeries
*/
/* == @section Accessibilité ==================== */
/* Texte pour les lecteurs d'écran… */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden; }

/* …et affiché au focus */
.screen-reader-text:focus {
  clip: auto !important;
  display: block;
  height: auto;
  left: 5px;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Passe par dessus la barre d'admin */ }

/* == @section Alignements ==================== */
.alignleft {
  display: inline;
  float: left;
  margin-right: 1.5em; }

.alignright {
  display: inline;
  float: right;
  margin-left: 1.5em; }

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto; }

/* == @section Réinitialisation flottants ==================== */
.clear:before,
.clear:after,
.entry-content:before,
.entry-content:after,
.comment-content:before,
.comment-content:after,
.site-header:before,
.site-header:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after {
  content: "";
  display: table;
  table-layout: fixed; }

.clear:after,
.entry-content:after,
.comment-content:after,
.site-header:after,
.site-content:after,
.site-footer:after {
  clear: both; }

/* == @section Widgets ==================== */
.widget {
  margin: 0 0 1.5em; }

/* Make sure select elements fit in widgets. */
.widget select {
  max-width: 100%; }

/* == @section Contenus ==================== */
/* -- Articles et pages ----------------------------- */
.sticky {
  display: block; }

.hentry {
  margin: 0 0 1.5em; }

.byline,
.updated:not(.published) {
  display: none; }

.single .byline,
.group-blog .byline {
  display: inline; }

.page-links {
  clear: both;
  margin: 0 0 1.5em; }

/* -- Commentaires ----------------------------- */
.comment-content a {
  word-wrap: break-word; }

.bypostauthor {
  display: block; }

/* == @section Médias ==================== */
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0; }

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%; }

/* -- Légendes ----------------------------- */
.wp-caption {
  margin-bottom: 1.5em;
  max-width: 100%; }

.wp-caption img[class*="wp-image-"] {
  display: block;
  margin-left: auto;
  margin-right: auto; }

.wp-caption .wp-caption-text {
  margin: 0.8075em 0; }

.wp-caption-text {
  text-align: center; }

/* -- Galeries ----------------------------- */
.gallery {
  margin-bottom: 1.5em; }

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%; }

.gallery-columns-2 .gallery-item {
  max-width: 50%; }

.gallery-columns-3 .gallery-item {
  max-width: 33.33%; }

.gallery-columns-4 .gallery-item {
  max-width: 25%; }

.gallery-columns-5 .gallery-item {
  max-width: 20%; }

.gallery-columns-6 .gallery-item {
  max-width: 16.66%; }

.gallery-columns-7 .gallery-item {
  max-width: 14.28%; }

.gallery-columns-8 .gallery-item {
  max-width: 12.5%; }

.gallery-columns-9 .gallery-item {
  max-width: 11.11%; }

.gallery-caption {
  display: block; }

